import React from 'react'

import Box from 'components/Box'
import Flex from 'components/Flex'
import BackgroundImage from 'components/BackgroundImage'
import Button from 'components/Button'
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'

import Link from 'i18n/Link'

import Layout from '../Layout';
import cover from './cover.svg'
import title from './title.svg'

const IndexPage = () => {
  return (
    <Layout height="100vh">
      <Box py="2.625em" bg="blue">
        <Box width="42%" mx="auto">
          <BackgroundImage src={title} ratio={116.05 / 577.04} />
        </Box>
      </Box>
      <Box pt="2em">
        <Box width="37.5%" mx="auto">
          <BackgroundImage src={cover} ratio={379.5 / 594.07} />
        </Box>
        <Box textAlign="center" pt="1.875em">
          <Button is={Link} fontSize="1.75em" to="/foreword">點擊進入測驗</Button>
        </Box>
      </Box>
    </Layout>
  )
}

export default IndexPage
